nav {	
	background-color: #6E0A05;
}

.nav-button{
	-webkit-transition: background-color 0.3s;
	-o-transition: background-color 0.3s;
	transition: background-color 0.3s;
}

.nav-button:hover {
	background-color: #8d2621;
}

.dropdown-button {
	width: 200px;
}

nav ul a{
	font-size: 20px;
}

nav .brand {
	font-family: 'Hammersmith One';
}


